@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
  padding: 0;
}

#entry-welcome {
  background: #e2d8e1;
  position: absolute;
  top: 0;
  z-index: 1;
  min-height: 100vh;
  width: 100vw;
  display: grid;
}

#entry-welcome:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: .5;
  z-index: -1;
  background: url(/minor-prophets/static/media/ezekiel.66644742.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

#welcome-middle {
  margin: auto;
  text-align: center;
  /*background: rgba(0, 0, 0, 0.15);*/
  /*border-radius: 10px;*/
  /*padding: 20px;*/
}

#welcome-middle img {
  height: 200px;
  width: 200px;
  border-radius: 100%;
  border: 2px solid white;
}

#welcome-middle h1 {
  margin-bottom: 0px;
}

#welcome-middle p {
  margin-top: 2px;
}

#welcome-middle i {
  font-size: 1.5em;
}

#prophet-wrapper {
  position: absolute;
  top: 0;
  z-index: -1;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  width: 100vw;
  background: #d3d8e0;
}

#cycle-wrapper {
  position: absolute;
  bottom: 20px;
  width: 100vw;
  display: grid;
}

#cycle-wrapper div {
  margin: auto;
}

#cycle-wrapper {
  font-size: 1.5em;
  color: black;
}

#cycle-wrapper i {
  margin-right: 20px;
  margin-left: 20px;
}

.hide-arrow {
  visibility: hidden;
}

.show-arrow {
  visibility: visible;
}

#prophet-info-container {
  height: 100%;
  padding-left: 40px;
  padding-right: 40px;
}

#prophet-header {
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
}

#prophet-header h1 {
  margin: 0;
  display: inline-block;
  font-family: "Nanum Gothic";
  color: black;
  margin-left: 20px;
  vertical-align: middle;
}

#prophet-header p {
  font-size: 0.8em;
}

#timeframe {
  margin-top: 5px;
  margin-bottom: 5px;
  font-weight: bold;
}

#key-verse {
  margin: 0 20% 0 20%;
  padding: 5px;
  background: #bdc2c9;
}

#prophet-headshot {
  height: 100px;
  width: auto;
  border: 1px solid white;
  border-radius: 10px;
  vertical-align: middle;
}

#prophet-bio {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  color: black;
}

#prophet-bio-left{
  display: flex;
  flex-direction: column;
  align-items: center;
}

#prophet-bio-left img {
  width: 100%;
  height: auto;
  box-shadow: 3px 3px 10px gray;
}

#prophet-bio-left span {
  font-size: 0.75em;
  font-style: italic;
  margin-left: 5px;
}

#prophet-bio-right {
  /*font-family: "Cinzel Decorative";*/
  font-family: "Crimson Text";
  padding-left: 40px;
  padding-right: 40px;
}

#prophet-bio-right p {
  text-indent: 50px;
  font-size: 1em;
}

#prophet-bio-right p:first-child {
  margin-top: 0;
}

/* Responsive media query */
@media screen and (max-width: 768px) {
  #prophet-bio {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }

  #prophet-bio-left img{
    width: 100%;
  }

  #prophet-wrapper {
    padding-bottom: 50px;
  }

  #prophet-bio-right {
    padding: 0;
    margin-top: 25px;
  }
}

